import { build, opts, createAppDiv, insertElement } from './core.js'

export const buildLegacyWidget = (options) => {
  build(options, true)

  const { dependencies:{ all } } = opts
  const   functions              = []

  for (let i = 0; i < all.length; i++)
    functions[i] = () => loadScript(all[i].url, functions[i+1])
  
  functions[0]()
}

function mounted (){
  const i18n                      = getI18n()
  const { compName, propsData }   = opts
  const VueClass                  = window['Vue'].extend(window[compName].default)
  const classInstanceProps        = i18n? { i18n, propsData } : { propsData }
  const vueClassInstance          = new VueClass(classInstanceProps)

  vueClassInstance.$mount()

  this.$el.appendChild(vueClassInstance.$el)
}

const loadSelf =  () => {
  const { cdnUrl } = opts //dependencies:{ vuePlugins },

  loadScript(cdnUrl+'/dist/legacy/umd/index.umd.min.js', loadApp)
}

const loadApp =  () => {
  createAppDiv()

  new window['Vue']({ mounted }).$mount(`#${opts.appId}`)
}

function loadScript (url, next = loadSelf){
  const tag = document.createElement('script')

  tag.charset            = 'utf-8'
  tag.nomodule           = true
  tag.src                = url
  tag.onload             = next
  tag.onreadystatechange = next

  insertElement(tag)
}

function getI18n(){
  if(window['VueI18n'])
    return new window['VueI18n']({ locale: 'en', fallbackLocale: 'en', messages: { en: {} } })
  return undefined
}